*{
	margin: 0;
	padding: 0;
	text-decoration: none;
	font-family: sans-serif;
	font-size: 14px;
	line-height: 20px;
}
p{
	-webkit-text-size-adjust: 200%;	
}
a{
	text-decoration: none;
}
a:visited{
	color: inherit;
}
body{
	min-height: 100vh;
	min-width: 600px;
	background-color: white;
	margin: 200px 32px 0 32px;
	width: calc(100% - 64px);
}

.Header-Top{
	position: fixed;
	top: 0;
	left: 0;
	background-color: #F4F5F6;
	height: 50px;
	box-shadow: 0 5px 10px #838691;
	width: 100%;
	min-width: 600px;
	z-index: 3;
}
.Header-Bottom{
	position: fixed;
	top: 50px;
	background-color: #FFFFFF;
	width: calc(100% - 64px);
	z-index: 2;
}
.Header-Top .Header-Left{
	left: 50px;
}
.Header-Left,
.Header-Right{
	position: absolute;
	display: flex;
	height: 50px;
	align-items: center;
	color: #808893;
	text-align: center;
	top: 0;
}
.Header-Right{
	right: 30px;
}
.Header-Bottom .Header-Left,
.Header-Bottom .Header-Right{
	padding: 7px 0;
}
.Header-Bottom .Header-Right{
	right: 0;
}

/*Header UI Logo*/
#UI-Logo-Link{
	display: inline-block;
	width: 50px;
	height: 50px;
}
#UI-Logo-Link .Logo-Background{
	fill: none;
}
#UI-Logo-Link .Logo-U{
	fill: #50565e;
}
#UI-Logo-Link:hover .Logo-Background{
	fill: white;
}
#UI-Logo-Link:hover .Logo-U{
	fill: #006FFF;
}
#UI-Logo-Link:focus{
	border: 1px solid #006FFF;
}
#UI-Logo-Link:focus .Logo-Background{
	fill: none;
}


/*Searchbar */
.search-container{
    display: flex;
    align-items: center;
    border: 1px solid rgba(0,0,0,0);
    border-radius: 5px;
    width: 344px;
    background-color: #F6F6F8;
}
#Searchbar{
    flex: 1;
    border: none;
    outline: none;
    padding: 6px;
    background-color: rgba(0,0,0,0);
}
.Search-Icon{
    font-size: 20px;
    color: #777;
    margin: 0 5px;
}
#Devices-Num{
	color: #BDBDBD;
	margin-left: 20px;
	font-size: 12px;
}

.Filters-Container{
	display: inline-flex;
}
.Filters-Container svg,
.Filters-Container div{
	padding: 6px;
}
