#Content{
	width: calc(100% - 64px);
	line-height: 20px;
	position: absolute;
	top: 152px;
	z-index: 1;
}

.List-Header{
	display: inline-flex;
	width: 100%;
	font-weight: 700;
	color: #000000;
	margin-top: 64px;
	border-bottom: 1px solid #EDEDF0;
}



.Item-Line{
	display: inline-flex;
	align-items: center;
	width: 100%;
	border-bottom: 1px solid #EDEDF0;
}


#Content img,
#Filler-Space{
	width: 36px;
	height: 36px;
}
#Content .column,
.List-Header .column{
	width: calc(50% - 18px);
	padding: 9px;
}
#Content a .column{
	color: rgba(0, 0, 0, 0.65);
}





